.home {

  .homeContent {
    background: rgb(15,8,131);
    background: linear-gradient(112deg, rgba(15,8,131,0.9192051820728291) 0%, rgba(9,58,121,0.5410539215686274) 35%, rgba(4,136,189,0.2553396358543417) 68%, rgba(0,212,255,0) 100%);    
    min-height: $min-height;
    width: 100%;
    // min-height: 700px;
    // height: 100%;
    border-radius: $borderRadius;
    position: relative;
    overflow: hidden;

    &:after {
      content: '';
      position: absolute;
      height: 100%;
      width: inherit;
      background: url('../../media/Softback.JPG') no-repeat center/cover;
      z-index: -1;
      filter: brightness(30%);
    }

    .content {
      position: absolute;
      top: 30%;
      left: 13%;
      font-family: $font-2;

      h1 {
        font-size: 5rem;
        font-family: $font-2;
        color: $color-5;
        text-transform: uppercase;
        text-shadow: 3px 6px 2px $black;
      }
      h2 {
        font-size: 2.5rem;
        padding: 1.5rem 0 7rem;
        color: $color-3;
        text-shadow: 1px 4px 1px $black;
      }
      .pdf {
        a {
          font-size: 1.5rem;
          background: $color-3;
          padding: 1rem 2rem;
          border-radius: $borderRadius;
          box-shadow: 0 5px 1px rgb(15, 15, 15);
          transition: 0.2s ease;
          // margin-bottom: 2rem;

          &:hover {
            background: $color-4;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 850px){
  .homeContent {
    margin: 0 !important;
  }
}
@media screen and (max-width:580px){
  

  
  .homeContent {
    
      
    &:after {
      
    background: url('../../media/Softback.JPG') no-repeat 60% 50%/cover !important;
    }
    h1 {
      font-size: 3rem !important;
    }
    h2 {
      font-size: 1.8rem !important;
    }
    
    
  }
 
  
}

