
// SIDEBAR (General settings: Background ans grids setup)
.sidebar{
    background: rgb(15,8,131);
    background: linear-gradient(192deg, rgba(15,8,131,1) 0%, rgba(9,58,121,1) 35%, rgba(4,136,189,1) 68%, rgba(0,212,255,1) 100%);
    font-family: $font-2;
    height: 100%;
    min-height: $min-height;
    width: 264px;
    border-radius: $borderRadius;
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 250px 1fr 108px;
    grid-template-areas: 
    "1"
    "2"
    "3";

//GRID AREA 1 (Profile picture + Name)

    .id {
        grid-area: 1;
        text-align: center;
        user-select: none;
        position: relative;

        .idContent {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;

            img {
                border-radius: 50%;
                box-shadow: 0 0 0 4px $color-5, 0 0 0 8px $color-1, 0 0 0 12px $color-3;
                width: 150px;
                height: 150px;
                pointer-events: none;
                
            }

            h3 {
                font-size: 1.3rem;
                text-transform: uppercase;
                letter-spacing: .15rem;
                margin-top: 1.2rem;
                color: $color-5;
            }
            &:hover h3 {
                transform: scale(1.1);
                transition: 1s ease;
            }
        }
    }

// GRID AREA 2 : NAVIGATION ROUTER = SLIGHT TRANSFORMATION OF ICONS    

            .nav {
                grid-area: 2;
                padding: 2rem 1rem;

               

                ul {
                    height: 100%;
                    display: grid;

                    

                    li {

                        padding: 1rem 0 1rem .4rem;
                        font-size: 1.3rem;
                        position: relative;
                        text-align: center;

                        

                        &:hover i {
                            transform: translate(5px, -5px)
                            rotate(-35deg);
                                                     

                        }

                        a {
                            color: $color-2;
                        }
    
                        .navActive {
                            color: $color-5;
                        }  

                        i {
                            position: absolute;
                            font-size: 1.2rem;
                            transform: translateY(3px) ;

                            
                            transition: 0.5s ease;
                          }

                          span {
                            margin-left: 2.7rem;
                          }

                          &:hover span  {
                            box-shadow: 2px 2.2px 3px 2px $color-2;

                          }

                    }

                    
                    
                                 
                }
            }

            .socialNetwork {
                grid-area: 3;
            
                ul {
                  display: flex;
                  width: 264px;
                  padding: 0 45px;
                  justify-content: space-between;
            
                  li {
                    font-size: 1.5rem;
                    background: $color-3;
                    color: $color-3;
                    border-radius: 50%;
                    padding: .5rem .8rem;
                    transition: .3s;
                    cursor: pointer;
            
                    a:visited {
                      color: $color-1;
                    }
                    // &:nth-child(1){
                    //   padding: .5rem .85rem
                    // }
                    // &:nth-child(2){
                    //   padding: .5rem .75rem
                    // }
                    &:hover {
                      background: $color-5;
                    }
                  }
                }
            }

            .footer {
                // margin-top: 2rem;
                text-align: center;
                letter-spacing: 0.1rem;
                font-size: .7rem;
                cursor: no-drop;
          
                a, a:visited {
                  color: $color-3;
          
                  &:hover {
                    color: $color-4;
                    
                  }
                }
              }
            }
         
            @media screen and (max-width: 850px){
                .sidebar {
                  display: flex;
                  min-height: 50px;
                  margin-bottom: .2rem;
                  width: 100%;
              
                  .id, .socialNetwork, .footer {
                    display: none;
                  }
                  .nav {
                    width: 100%;
                    padding: .1rem 1rem;
              
                    ul {
                      display: flex;
                      justify-content: space-between;
              
                      span {
                        margin-left: 1.5rem !important;
                      }
                    }
                  }
                }
              }
              @media screen and (max-width:580px){
                .sidebar {
                  ul {
                    justify-content: space-around !important;
              
                    li {
                      padding: 1rem 0 !important;
                    }
                    i {
                      font-size: 1.4rem !important;
                      transform: translateY(-3px) !important;
                    }
                    span {
                      display: none;
                    }
                  }
                }
              }
                 
