@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@800&family=Josefin+Sans:wght@100&family=Press+Start+2P&display=swap");


$font-1: 'Press Start 2P', cursive;
$font-2: 'Dosis', sans-serif;
$font-3: 'Josefin Sans', serif;

$color-1: #452c63;
$color-2: #4B0082;
$color-3: #E6E6FA;
$color-4:#662d91;
$color-5: #D8BFD8;
$white: #ddd;
$black: #333;

$borderRadius: 25px;
$shadow: 0 2px 3px #333;
$min-height: 95vh;

@mixin verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 1rem auto;
    max-width: 1280px;
    font-family: $font-2;
    color: $black;
    background-color: $black;
   
}

li {
    list-style-type: none;
}

// button {
//     cursor: pointer;
// }

a {
    text-decoration: none;
    color: $black;
    // cursor: pointer;
}

a:visited {
    color: $black
}


//NavBar dynamically reacts to the screen with the different components
.home, .skills, .portfolio, .contact {
    display: flex;

    @media screen and (max-width: 850px){
        display: block !important;
        
    }
}

.homeContent, .skillsContent, .portfolioContent, .contactContent {
    margin-left: .4rem;
    @media screen and (max-width: 850px){
        margin-left: 0 !important;
        
    }
}